import { useContext, useState } from 'react';
import { motion as m } from 'framer-motion';
import styles from './LeftFilterBar.module.css';
import TagsSection from './../TagsSection/TagsSection';
import SearchFilterContext from '../../utils/context/SearchFilterContext';
import { useFilter } from '../../utils/hooks/useFilter';
import TagButton from '../TagButton/TagButton';
import { useLocation } from 'react-router-dom';

const LeftFilerBar = ({ handleSearch, searchQuery }) => {
  const [showAllDates, setShowAllDates] = useState(false);
  const [showAllSpecialities, setShowAllSpecialities] = useState(false);
  const location = useLocation();
  const { values, setValues, findValues, setFindValues } =
    useContext(SearchFilterContext);
  const resultLocation = location.pathname === '/results';

  const {
    handleInputChange,
    handleDateBlur,
    handleButtonChange,
    setItemOnClick,
  } = useFilter({
    values,
    setValues,
    findValues,
    setFindValues,
  });

  //console.log(values)

  const handleSearchClick = () => {
    handleSearch();
  };

  const toggleShowAllDates = () => {
    setShowAllDates(!showAllDates);
  };

  const toggleShowAllSpecialities = () => {
    setShowAllSpecialities(!showAllSpecialities);
  };

  const renderDateOptions = () => {
    const dateOptions = [
      { id: 'today', value: 'Today', label: '' },
      { id: 'tomorrow', value: 'Tomorrow', label: '' },
      { id: 'thisweekend', value: 'This weekend', label: '  ' },
      { id: 'pickdate', value: 'Pick date', label: ' ' },
    ];

    if (showAllDates) {
      dateOptions.push(
        { id: 'thisweek', value: 'This week', label: '  ' },
        { id: 'thismonth', value: 'This month', label: '  ' },
        { id: 'nextmonth', value: 'Next month', label: '  ' }
      );
    }

    return dateOptions.map((option) => (
      <label htmlFor={option.id} className={styles.radioButton} key={option.id}>
        <input
          onChange={handleInputChange}
          id={option.id}
          type="radio"
          value={option.label}
          name="date"
          checked={values.date === option.label}
        />
        <span className={`${option.id === 'pickdate' && styles.radioText}`}>
          {option.label}
        </span>
        {option.id === 'pickdate' && (
          <input
            onChange={handleInputChange}
            className={styles.pickdate}
            name="date"
            type="date"
            onBlur={handleDateBlur}
            min={new Date()}
          ></input>
        )}
      </label>
    ));
  };

  const renderSpecialityOptions = () => {
    const specialityOptions = [
      { id: 'backend', label: 'Backend' },
      { id: 'frontend', label: 'Frontend' },
      { id: 'qa', label: 'QA' },
      { id: 'uxui', label: 'UX/UI ' },
    ];

    if (showAllSpecialities) {
      specialityOptions.push(
        { id: 'web', label: 'Web-' },
        { id: 'datascience', label: 'Data Science' }
      );
    }

    return specialityOptions.map((option) => (
      <label htmlFor={option.id} key={option.id}>
        <input
          onChange={handleInputChange}
          id={option.id}
          type="checkbox"
          value={option.label}
          name="specialities"
          checked={values.specialities.includes(option.label)}
          className={styles.checkboxButton}
        />
        <span className={styles.checkboxLabel}>{option.label}</span>
      </label>
    ));
  };

  return (
    <m.section
      initial={{ x: -100, opacity: 0 }} //   -    100 
      animate={{ x: 0, opacity: 1 }} //   -  
      transition={{ duration: 0.7 }} //  
      className={styles.filterForm}
    >
      {resultLocation && <h2 className={styles.filterTitle}></h2>}
      <ul className={styles.filterList}>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <label htmlFor="online">
            <input
              onChange={handleInputChange}
              id="online"
              name="status"
              value="Online"
              type="checkbox"
              className={styles.checkboxButton}
              checked={values.status.includes('Online')}
            />
            <span className={styles.checkboxLabel}>Online</span>
          </label>
          <label htmlFor="offline">
            <input
              onChange={handleInputChange}
              id="offline"
              name="status"
              value="Offline"
              type="checkbox"
              className={styles.checkboxButton}
              checked={values.status.includes('Offline')}
            />
            <span className={styles.checkboxLabel}>Offline</span>
          </label>
        </li>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <input
            onChange={handleInputChange}
            name="city"
            type="text"
            value={values.city || ''}
            placeholder=" "
            className={styles.filterInput}
          />
          {findValues && findValues.city && findValues.city !== '' && (
            <div className={styles.serchContainer}>
              {findValues.city.map((item, index) => {
                return (
                  <button
                    onClick={() => setItemOnClick({ city: item })}
                    className={styles.findItem}
                    key={index}
                  >
                    {item}
                  </button>
                );
              })}
            </div>
          )}
        </li>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <div className={styles.lessBlock}>
            {renderDateOptions()}
            <button onClick={toggleShowAllDates} className={styles.showMore}>
              {showAllDates ? ' ' : ' '}
            </button>
          </div>
        </li>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <div className={styles.lessBlock}>
            {renderSpecialityOptions()}
            <button
              onClick={toggleShowAllSpecialities}
              className={styles.showMore}
            >
              {showAllSpecialities ? ' ' : ' '}
            </button>
          </div>
        </li>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <label htmlFor="free" className={styles.radioButton}>
            <input
              onChange={handleInputChange}
              id="free"
              type="radio"
              value=""
              name="price"
              checked={values.price === ''}
            />
            <span></span>
          </label>
          <label htmlFor="paid" className={styles.radioButton}>
            <input
              onChange={handleInputChange}
              id="paid"
              type="radio"
              value=""
              name="price"
              checked={values.price === ''}
            />
            <span></span>
          </label>
        </li>
        <li>
          <h3 className={styles.itemTitle}></h3>
          <input
            onChange={handleInputChange}
            name="findTags"
            type="text"
            value={values.findTags || ''}
            placeholder=" "
            className={styles.filterInput}
          />
          {findValues && findValues.findTags && findValues.findTags !== '' && (
            <div className={styles.serchContainer}>
              <div className={styles.tagsList}>
                {findValues.findTags.map((item, index) => {
                  return (
                    <TagButton
                      key={index}
                      value={item}
                      handleChange={handleButtonChange}
                    />
                  );
                })}
              </div>
            </div>
          )}
        </li>
      </ul>
      <TagsSection handleChange={handleButtonChange} />
      {location.pathname === '/' && (
        <button
          onClick={handleSearchClick}
          className={styles.buttonSearch}
          type="button"
        >
          
        </button>
      )}
    </m.section>
  );
};

export default LeftFilerBar;
